.header {
    text-align: center;
    padding-top: 15px;
    color: var(--color-yellow);
    position: absolute;
    width: 100%;
    &.has-background {
        position: relative;
        background-image: url(../../../images/brick.jpg);
        background-position: center;
        background-size: 100%;
    }
    >.yellow-line {
        position: absolute;
        background-color: var(--color-yellow);
        height: 4px;
        width: calc(50% - var(--size-margin) - 200px);
        top: calc(var(--size-header-font-size) / 2 + 10px);
        &.left {
            left: var(--size-margin);
        }
        &.right {
            right: var(--size-margin);
        }
    }
}

@media screen and (max-width: 600px) {
    .header {
        >.lines {
            display: none;
        }
    }
}