.place-details-photo {
    height: 33vh;
    width: 33vh;
    background-size: cover;
    background-position: center;
    margin-bottom: var(--size-small-margin);
    position: relative;
    border: solid 2px var(--color-yellow);
    >.place-details-photo-caption, >.place-details-photo-photo-credits {
        opacity: 0;
        transition: opacity 0.4s;
        position: absolute;
        color: var(--color-yellow);
    }
    >.place-details-photo-caption {
        font-family: ReenieBeanie;
        font-size: var(--size-secondary-font-size);
        text-align: center;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
    }
    >.place-details-photo-photo-credits {
        font-family: AmaticSC;
        bottom: 0;
        right: 2px;
    }
    >.place-details-photo-background-hover {
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: var(--color-off-black);
        opacity: 0;
        transition: opacity 0.4s;
    }
    &:hover {
        >.place-details-photo-background-hover {
            opacity: 0.5;
        }
        >.place-details-photo-caption, >.place-details-photo-photo-credits {
            opacity: 1;
        }
    }
}

@media screen and (max-width: 600px) {
    .place-details-photo {
        height: calc(100vw - (var(--size-margin) * 2));
        width: calc(100vw - (var(--size-margin) * 2));
    }
}